<template>
    <div v-if="section">
        <div style="display: flex; justify-content: space-between; align-items: center" class="mb-base">
            <div>
                <div style="display: flex; align-items: center; cursor: pointer" >
                    <feather-icon class="mr-2" icon="ArrowLeftIcon" style="width: 18px; margin-bottom: 2px;" @click="$router.go(-1)"></feather-icon>
                    <h3 class="mb-1" style="padding-left: 10px; border-left: 1px solid silver">Модули секции</h3>
                </div>
            </div>

            <vs-button color="primary" icon-pack="feather" icon="icon-plus" @click="isPrompt = true">Добавить модуль</vs-button>
        </div>

        <vs-divider class="section-title mb-5" position="left" border-style="dashed">
            <div style="display: flex; align-items: center; padding: 0">
                <feather-icon class="mr-2" icon="GridIcon"></feather-icon>
                <h5>{{ section.name }}</h5>
            </div>
        </vs-divider>

        <div class="vx-row">

            <div class="vx-col w-full lg:w-1/4 md:w-1/2 mb-2" v-for="module in section.modules">
                <vs-card>
                    <div style="display: flex; justify-content: space-between">
                        <div style="display: flex; align-items: center; white-space: nowrap; overflow: hidden;"
                             class="p-2">
                            <feather-icon class="mr-4" icon="GitCommitIcon"></feather-icon>
                            <div>
                                <h5 class="mb-1">{{ module.name }}</h5>
                                ID: {{ module.device_id }}
                            </div>
                        </div>
                        <div style="display: flex; align-items: center" class="p-2">
                            <vs-button size="small" class="mx-2" color="primary" type="border" icon-pack="feather"
                                       icon="icon-edit-2" @click="edit(module)"></vs-button>
                            <vs-button size="small" color="danger" type="border" icon-pack="feather"
                                       icon="icon-trash-2" @click="remove(module)"></vs-button>
                        </div>
                    </div>
                </vs-card>
            </div>

        </div>

        <vs-prompt
            :title="current.id ? 'Редакировать модуль' : 'Новый модуль'"
            accept-text="Сохранить"
            cancel-text="Отменить"
            @cancel="cancel"
            @accept="save"
            @close="cancel"
            :active.sync="isPrompt">
            <div class="con-exemple-prompt">
                <span>Введите название</span>
                <vs-input placeholder="А1" vs-placeholder="А1" v-model="current.name" @keyup.enter="save" class="mt-3 mb-5 w-full"/>

                <span>Введите ID модуля</span>
                <vs-input placeholder="201905040001" vs-placeholder="201905040001" @keyup.enter="save" v-model="current.device_id" class="mt-3 w-full"/>
            </div>
        </vs-prompt>

    </div>
</template>

<script>
    export default {
        data(){
            return {
                section: null,
                isPrompt: false,
                current: {
                    id: null,
                    name: '',
                    device_id: '',
                    section_id: '',
                }
            }
        },
        created(){
            this.getList()
        },
        mounted(){
            this.$io.on('model:update:modules', () => {
                this.getList();
            })
        },
        beforeDestroy(){
            this.$io.on('model:update:modules', () => {
                this.getList();
            })
        },
        methods: {
            async getList(){
                try{
                    let response = await this.$http.get('/sections/' + this.$route.params.id);
                    this.section = response.data
                }catch (e) {
                    if(e.response.status === 404){
                        this.$router.push('/error-404')
                    }else{
                        this.$vs.dialog({
                            type: 'confirm',
                            color: 'warning',
                            title: `Ошибка`,
                            text: 'Не удалось получить данные, повторите попытку',
                            accept: this.getList,
                            acceptText: "Повторить",
                            cancelText: "Отменить"
                        })
                    }
                }
            },
            open(id) {
                this.$router.push('/sections/' + id);
            },
            edit(section) {
                this.current = {
                    id: section.id,
                    name: section.name,
                    device_id: section.device_id,
                    section_id: this.section.id,
                };
                this.isPrompt = true;
            },
            remove(section) {
                this.current = {
                    id: section.id,
                    name: section.name,
                    device_id: section.device_id,
                    section_id: this.section.id,
                };
                this.$vs.dialog({
                    type: 'confirm',
                    color: 'danger',
                    title: `Подтвердите действие`,
                    text: 'Внимание! Данное действие невозможно отменить!',
                    accept: this.delete,
                    cancel: this.cancel,
                    acceptText: "Удалить",
                    cancelText: "Отменить"
                })
            },
            cancel(){
                this.isPrompt = false;
                this.current = {
                    id: null,
                    name: '',
                    device_id: '',
                    section_id: '',
                }
            },
            async save(){
                try{
                    if(this.current.id){
                        await this.$http.post('/modules/' + this.current.id, {
                            name: this.current.name,
                            device_id: this.current.device_id,
                            section_id: this.section.id
                        });
                    }else{
                        await this.$http.post('/modules', {
                            name: this.current.name,
                            device_id: this.current.device_id,
                            section_id: this.section.id
                        });
                    }

                    this.$vs.notify({
                        color: 'success',
                        title: 'Успешно',
                        text: 'Данные сохранены'
                    });

                    this.cancel();
                }catch (e) {
                    if(e.response.status === 403){
                        this.$vs.notify({
                            color: 'warning',
                            title: `Ошибка`,
                            text: e.response.data.message,
                        });
                        this.isPrompt = true;
                    }else{
                        this.$vs.dialog({
                            type: 'confirm',
                            color: 'warning',
                            title: `Ошибка`,
                            text: 'Не удалось сохранить данные, повторите попытку',
                            accept: this.save,
                            acceptText: "Повторить",
                            cancelText: "Отменить"
                        })
                    }
                }
            },
            async delete(){
                try {
                    await this.$http.delete('/modules/' + this.current.id);
                    this.$vs.notify({
                        color: 'success',
                        title: 'Успешно',
                        text: 'Запись удалена'
                    });

                    this.cancel();
                }catch (e) {
                    this.$vs.dialog({
                        type: 'confirm',
                        color: 'warning',
                        title: `Ошибка`,
                        text: 'Не удалось сохранить данные, повторите попытку',
                        accept: this.delete,
                        acceptText: "Повторить",
                        cancelText: "Отменить"
                    })
                }
            }
        }
    }
</script>

<style>
    .section-title > .vs-divider--text {
        background: transparent !important;
        padding-left: 5px !important;
    }
</style>
